import { useContext } from "react";
import { ThemeContext } from "../Theme/ThemeContext";

function SwitchToggle(props) {
  const [isDarkMode, setIsDarkMode] = useContext(ThemeContext);

  const toggleTheme = () => {
    setIsDarkMode(!isDarkMode);

    if (!isDarkMode) {
      document.documentElement.classList.add("dark");
    } else {
      document.documentElement.classList.remove("dark");
    }
  };

  const darkMode = (
    <svg
      xmlns="http://www.w3.org/2000/svg"
      className="h-full w-full"
      fill="none"
      viewBox="0 0 24 24"
      stroke="currentColor"
    >
      <path
        strokeLinecap="round"
        strokeLinejoin="round"
        strokeWidth={2}
        d="M12 3v1m0 16v1m9-9h-1M4 12H3m15.364 6.364l-.707-.707M6.343 6.343l-.707-.707m12.728 0l-.707.707M6.343 17.657l-.707.707M16 12a4 4 0 11-8 0 4 4 0 018 0z"
      />
    </svg>
  );

  const lightMode = (
    <svg
      xmlns="http://www.w3.org/2000/svg"
      className="h-6 w-6"
      fill="none"
      viewBox="0 0 24 24"
      stroke="currentColor"
    >
      <path
        strokeLinecap="round"
        strokeLinejoin="round"
        strokeWidth={2}
        d="M20.354 15.354A9 9 0 018.646 3.646 9.003 9.003 0 0012 21a9.003 9.003 0 008.354-5.646z"
      />
    </svg>
  );
  const mode = isDarkMode ? darkMode : lightMode;
  return (
    // fixed bottom-0 right-0 p-2 bg-white-main mr-5 mb-5    
    <div className="rounded-full cursor-pointer z-30" onClick={toggleTheme}>
      <div className="flex justify-center items-center md:w-6 md:h-6 w-6 h-6 dark:text-white-main">
          {mode}
      </div>
    </div>

  );
}

export default SwitchToggle;
